.Home {
  width: 100%;
  min-height: calc(100vh - 60px);
  background: url('../../images/stars.svg'), #1B1B25;
  background-repeat: repeat;

  color: #ffffff;

  display: grid;
	grid-template: 1fr / 100px300px1fr 100px;	
	grid-template-areas: ". text image .";
	align-items: center;
	justify-items: center;
}

.col-img{
	grid-area: image;	
}

.col-imgimg{	
	width: 100%;
}

.col-text{
	grid-area: text
}

.col-textimg{
	margin-bottom: 20px;
}